import {ValueDate} from './value-date';
import {DateRangePickerOptions} from '../date-range-picker-options';
import {TranslateService} from '@ngx-translate/core';

export class ValueDateTime extends ValueDate {
    protected get baseOptions(): DateRangePickerOptions {
        return new DateRangePickerOptions('DD/MM/YYYY HH:mm', true, true, true, this.translateService).setRestParameterFormat('YYYY/MM/DD HH:mm:ss');
    }
}
